<template>
  <div>
    <header>
      <a class="return" @click="$router.go(-1)"><i class="iconfont icon-return"></i></a>
      <h1></h1>
    </header>
    <section>
      {{name}} {{name}}
      {{name}}<br/><br/>
      {{name}}<br/><br/>
      {{name}}{{name}}<br/><br/>
      {{name}}<br/><br/>
      {{name}}{{name}}30300{{name}}<br/><br/>
      {{name}}<br/><br/>
      {{name}}<br/>
    </section>
  </div>
</template>
<script>
import api from "../api.js"
export default {
  data(){
    return{
      name:api.name
    }
  }
}
</script>
<style scoped>
  section {
    padding-left: 1rem;
    padding-right: 1rem;
    line-height: 2rem;
    font-size: 12px;
    background-color: #fff;
  }
</style>



