import loadingVue from './loading.vue';

//
let LoadingBox;
export default {
  install(Vue) {
    const LoadingPlugin = Vue.extend(loadingVue);// 
    LoadingBox = new LoadingPlugin({
      el: document.createElement('div') // DOM
    });
    document.body.appendChild(LoadingBox.$el);
    Vue.prototype.$loading = {
      show(msg) {
        LoadingBox.show(msg)
      },
      hide() {
        LoadingBox.hide()
      }
    }
  }
}



